import React from "react";
import PropTypes from "prop-types";
import style from "./SectionEventsItem.module.css";

const SectionEventsItem = ({ id, title, image, date, time, description }) => {
  return (
    <li key={id} className={style.listItem}>
      <img
        className={style.listImg}
        src={image}
        alt={title}
        width="72"
        height="72"
      />
      <div className={style.listDescr}>
        <p className={style.listHeadline}>{title}</p>
        <div className={style.dateTime}>
          <p className={style.listDate}>{date}</p>
          <p className={style.listTime}>{time}</p>
        </div>
        <p className={style.listText}>{`${description.slice(0, 90)}...`}</p>
      </div>
    </li>
  );
};

SectionEventsItem.propTypes = {
  id: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  image: PropTypes.string.isRequired,
  date: PropTypes.string.isRequired,
  time: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired
};

export default SectionEventsItem;
