import React from "react";
import PropTypes from "prop-types";
import "date-fns";
import DateFnsUtils from "@date-io/date-fns";
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker
} from "@material-ui/pickers";
import style from "./SectionReservation.module.css";

const SectionReservation = ({
  changeDate,
  date,
  changeTime,
  time,
  name,
  tel,
  numberOfPeople,
  comments,
  handleChange,
  handleSubmit,
  isEmptyInput
}) => {
  return (
    <div className={style.reservationWrapper}>
      <p
        className={
          isEmptyInput
            ? `${style.headline}  ${style.headlineEmpty}`
            : `${style.headline}`
        }
      >
        Make a Reservation
      </p>
      {isEmptyInput && (
        <p className={style.inputError}>Please fill in the required inputs</p>
      )}
      <form
        method="post"
        id="form"
        className={style.reservationForm}
        onSubmit={handleSubmit}
      >
        <MuiPickersUtilsProvider
          utils={DateFnsUtils}
          className={style.pickersOverlay}
        >
          <KeyboardDatePicker
            disableToolbar
            variant="inline"
            disablePast
            format="MM/dd/yyyy"
            id="date-picker"
            label="Date*"
            value={date}
            onChange={changeDate}
            KeyboardButtonProps={{
              "aria-label": "change date"
            }}
            InputProps={{
              className: style.picker
            }}
          />
          <KeyboardTimePicker
            disableToolbar
            variant="inline"
            id="time-picker"
            label="Time*"
            value={time}
            onChange={changeTime}
            KeyboardButtonProps={{
              "aria-label": "change time"
            }}
            InputProps={{
              className: style.picker
            }}
          />
        </MuiPickersUtilsProvider>
        <input
          className={`${style.input} ${style.inputName}`}
          type="text"
          name="name"
          value={name}
          placeholder="Name*"
          onChange={handleChange}
        />
        <input
          className={`${style.input} ${style.inputTime}`}
          type="tel"
          name="tel"
          value={tel}
          placeholder="Tel*"
          onChange={handleChange}
        />
        <input
          className={`${style.input} ${style.inputName}`}
          type="number"
          name="numberOfPeople"
          value={numberOfPeople}
          placeholder="Number of people*"
          onChange={handleChange}
        />
        <input
          className={`${style.input} ${style.inputComments}`}
          type="text"
          name="comments"
          value={comments}
          placeholder="Comments"
          onChange={handleChange}
        />
        <button type="submit" className={style.button}>
          find a table
        </button>
      </form>
    </div>
  );
};

SectionReservation.defaultProps = {
  date: null,
  time: null,
  name: "",
  tel: "",
  numberOfPeople: "",
  comments: "",
  changeDate: () => {},
  changeTime: () => {},
  handleChange: () => {},
  handleSubmit: () => {}
};

SectionReservation.propTypes = {
  changeDate: PropTypes.func,
  date: PropTypes.shape(),
  changeTime: PropTypes.func,
  time: PropTypes.shape(),
  name: PropTypes.string,
  tel: PropTypes.string,
  numberOfPeople: PropTypes.string,
  comments: PropTypes.string,
  handleChange: PropTypes.func,
  handleSubmit: PropTypes.func,
  isEmptyInput: PropTypes.bool.isRequired
};

export default SectionReservation;
