import React, { PureComponent } from "react";
import { Switch, Route } from "react-router-dom";
import "./App.css";
import Header from "../Header/Header";
import Footer from "../Footer/Footer";
import HomePage from "../../pages/HomePage/HomePage";
import MenuPage from "../../pages/MenuPage/MenuPage";
import AboutPage from "../../pages/AboutPage/AboutPage";
import EventsPage from "../../pages/EventsPage/EventsPage";

class App extends PureComponent {
  state = {
    isModalOpen: false,
    date: null,
    time: null,
    name: "",
    tel: "",
    numberOfPeople: "",
    comments: "",
    isEmptyInput: false
  };

  openModal = () => {
    this.setState({
      isModalOpen: true
    });
  };

  closeModal = () => {
    this.setState({
      isModalOpen: false
    });
  };

  scrollDownForDetails = () => {
    window.scrollTo({
      top: 700,
      behavior: "smooth"
    });
  };

  scrollToTop = () => {
    window.scrollTo({
      top: 0
    });
  };

  scrollToDown = () => {
    window.scrollTo({
      top: 700
    });
  };

  changeDate = date => {
    this.setState({ date });
  };

  changeTime = time => {
    this.setState({ time });
  };

  handleChange = ({ target }) => {
    const { name, value } = target;
    this.setState({ [name]: value });
  };

  handleSubmit = evt => {
    const { date, time, numberOfPeople } = this.state;

    evt.preventDefault();

    if (!date || !time || !numberOfPeople) {
      this.setState({
        isEmptyInput: true
      });
    } else {
      this.closeModal();
      this.setState({
        date: null,
        time: null,
        name: "",
        tel: "",
        numberOfPeople: "",
        comments: ""
      });
    }
  };

  render() {
    const {
      isModalOpen,
      date,
      time,
      name,
      tel,
      numberOfPeople,
      comments,
      isEmptyInput
    } = this.state;

    return (
      <div>
        <Header
          isModalOpen={isModalOpen}
          openModal={this.openModal}
          closeModal={this.closeModal}
          scrollDownForDetails={this.scrollDownForDetails}
          scrollToTop={this.scrollToTop}
          changeDate={this.changeDate}
          date={date}
          changeTime={this.changeTime}
          time={time}
          name={name}
          tel={tel}
          numberOfPeople={numberOfPeople}
          comments={comments}
          handleChange={this.handleChange}
          handleSubmit={this.handleSubmit}
          isEmptyInput={isEmptyInput}
        />
        <Switch>
          <Route
            render={() => (
              <HomePage
                scrollToDown={this.scrollToDown}
                changeDate={this.changeDate}
                date={date}
                changeTime={this.changeTime}
                time={time}
                name={name}
                tel={tel}
                numberOfPeople={numberOfPeople}
                comments={comments}
                handleChange={this.handleChange}
                handleSubmit={this.handleSubmit}
                isEmptyInput={isEmptyInput}
              />
            )}
            path="/"
            exact
          />
          <Route component={MenuPage} path="/menu" />
          <Route component={AboutPage} path="/about" />
          <Route component={EventsPage} path="/events" />
        </Switch>
        <Footer
          scrollToTop={this.scrollToTop}
          scrollToDown={this.scrollToDown}
        />
      </div>
    );
  }
}

export default App;
