import React from "react";
import PropTypes from "prop-types";
import style from "./Events.module.css";
import EventItem from "./EventItem/EventItem";

const Events = ({
  events,
  selectedEventsIds,
  showDescription,
  closeDescription
}) => {
  return (
    <div className={style.eventsWrapper}>
      <p className={style.title}>Upcoming Events</p>
      <ul className={style.eventsList}>
        {events.map(el => (
          <EventItem
            key={el.id}
            id={el.id}
            image={el.image}
            title={el.title}
            date={el.date}
            time={el.time}
            description={el.description}
            showDescription={showDescription}
            closeDescription={closeDescription}
            selectedEventsIds={selectedEventsIds}
          />
        ))}
      </ul>
    </div>
  );
};

Events.defaultProps = {
  selectedEventsIds: []
};

Events.propTypes = {
  events: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string.isRequired,
      image: PropTypes.string.isRequired,
      title: PropTypes.string.isRequired,
      date: PropTypes.string.isRequired,
      time: PropTypes.string.isRequired,
      description: PropTypes.string.isRequired
    }).isRequired
  ).isRequired,
  selectedEventsIds: PropTypes.arrayOf(PropTypes.string),
  showDescription: PropTypes.func.isRequired,
  closeDescription: PropTypes.func.isRequired
};

export default Events;
