import React from "react";
import PropTypes from "prop-types";
import style from "./Gallery.module.css";
import images from "../../helpers/gallery.json";
import ModalGallery from "../ModalGallery/ModalGallery";

const Gallery = ({
  isModalGalleryOpen,
  openModal,
  closeModal,
  selectedImgInd,
  showLargeImg
}) => {
  return (
    <div className={style.galleryOverlay}>
      <p className={style.title}>Our restaurant</p>
      <ul className={style.galleryList}>
        {images.map(el => (
          <li
            key={el.id}
            className={style.galleryItem}
            onClick={() => openModal(images.indexOf(el))}
          >
            <img className={style.image} src={el.image} alt="Our restaurant" />
          </li>
        ))}
        {isModalGalleryOpen && (
          <ModalGallery
            closeModal={closeModal}
            selectedImgInd={selectedImgInd}
            images={images}
            showLargeImg={showLargeImg}
          />
        )}
      </ul>
    </div>
  );
};

Gallery.defaultProps = {
  selectedImgInd: null,
  showLargeImg: () => {}
};

Gallery.propTypes = {
  isModalGalleryOpen: PropTypes.bool.isRequired,
  openModal: PropTypes.func.isRequired,
  closeModal: PropTypes.func.isRequired,
  selectedImgInd: PropTypes.number,
  showLargeImg: PropTypes.func
};

export default Gallery;
