import React from "react";
import PropTypes from "prop-types";
import style from "./EventItem.module.css";

const EventItem = ({
  id,
  image,
  title,
  date,
  time,
  description,
  showDescription,
  closeDescription,
  selectedEventsIds
}) => {
  const selectedEvent = selectedEventsIds.find(el => el === id);

  return (
    <li className={style.eventItem}>
      <img className={style.image} src={image} alt={title} />
      <div className={style.text}>
        <p className={style.title}>{title}</p>
        <div className={style.dateTime}>
          <p className={style.date}>{date}</p>
          <p className={style.time}>{time}</p>
        </div>
        {selectedEvent === id ? (
          <div>
            <p className={style.description}>{description}</p>
            <button
              type="button"
              className={style.btnClose}
              onClick={() => closeDescription(id)}
            >
              close
            </button>
          </div>
        ) : (
          <div>
            <p className={style.description}>{`${description.slice(
              0,
              350
            )}...`}</p>
            <button
              type="button"
              className={style.btnReadMore}
              onClick={() => showDescription(id)}
            >
              show more...
            </button>
          </div>
        )}
      </div>
    </li>
  );
};

EventItem.defaultProps = {
  selectedEventsIds: []
};

EventItem.propTypes = {
  id: PropTypes.string.isRequired,
  image: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  date: PropTypes.string.isRequired,
  time: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  selectedEventsIds: PropTypes.arrayOf(PropTypes.string),
  showDescription: PropTypes.func.isRequired,
  closeDescription: PropTypes.func.isRequired
};

export default EventItem;
