import React, { Component, createRef } from "react";
import PropTypes from "prop-types";
import style from "./Modal.module.css";
import SectionReservation from "../SectionReservation/SectionReservation";

class Modal extends Component {
  backdropRef = createRef();

  UNSAFE_componentWillMount() {
    window.removeEventListener("keydown", this.handlePressKey);
  }

  componentDidMount() {
    window.addEventListener("keydown", this.handlePressKey);
  }

  handlePressKey = evt => {
    if (evt.code !== "Escape") return;
    this.props.closeModal();
  };

  handleBackdropClick = evt => {
    const { current } = this.backdropRef;
    if (current && evt.target !== current) return;
    this.props.closeModal();
  };

  render() {
    const {
      changeDate,
      date,
      changeTime,
      time,
      name,
      tel,
      numberOfPeople,
      comments,
      handleChange,
      handleSubmit,
      isEmptyInput
    } = this.props;

    return (
      <div
        className={style.overlay}
        role="button"
        tabIndex={0}
        ref={this.backdropRef}
        onClick={this.handleBackdropClick}
      >
        <div className={style.modal}>
          <SectionReservation
            changeDate={changeDate}
            date={date}
            changeTime={changeTime}
            time={time}
            name={name}
            tel={tel}
            numberOfPeople={numberOfPeople}
            comments={comments}
            handleChange={handleChange}
            handleSubmit={handleSubmit}
            isEmptyInput={isEmptyInput}
          />
        </div>
      </div>
    );
  }
}

Modal.defaultProps = {
  date: null,
  time: null,
  numberOfPeople: "",
  comments: ""
};

Modal.propTypes = {
  closeModal: PropTypes.func.isRequired,
  changeDate: PropTypes.func.isRequired,
  date: PropTypes.shape(),
  changeTime: PropTypes.func.isRequired,
  time: PropTypes.shape(),
  name: PropTypes.string.isRequired,
  tel: PropTypes.string.isRequired,
  numberOfPeople: PropTypes.string,
  comments: PropTypes.string,
  handleChange: PropTypes.func.isRequired,
  handleSubmit: PropTypes.func.isRequired,
  isEmptyInput: PropTypes.bool.isRequired
};

export default Modal;
