import React from "react";
import { NavLink } from "react-router-dom";
import PropTypes from "prop-types";
import style from "./SectionMenu.module.css";

const SectionMenu = ({ scrollToDown }) => {
  return (
    <section className={style.menu}>
      <div className={style.pictures}>
        <div className={style.topPictures}>
          <img className={style.pic1} src="../images/menu-1.jpg" alt="" />
          <img className={style.pic2} src="../images/menu-2.jpg" alt="" />
        </div>
        <img className={style.pic3} src="../images/menu-3.jpg" alt="" />
      </div>
      <div className={style.description}>
        <h3 className={style.headline}>Tasteful French Cuisine</h3>
        <p className={style.text}>
          Lorem Ipsum roin gravida nibh vel velit auctor aliquet. Aenean
          sollicitudin, lorem quis bibendum auctor, nisi elit{" "}
          <span className={style.textDecor}>consequat ipsum</span>, nec sagittis
          sem nibh id elit.
        </p>
        <p className={style.text}>
          Duis sed odio sit amet nibh vulputate cursus a sit amet mauris. Morbi
          accumsan ipsum velit. Nam nec{" "}
          <span className={style.textDecor}>tellus a odio</span> tincidunt
          auctor a ornare odio. This duis sed odio sit amet nibh cursus a sit
          amet mauris. Morbi accumsan ipsum vulputate.
        </p>
        <NavLink to="/menu" className={style.linkMenu} onClick={scrollToDown}>
          view menu
        </NavLink>
      </div>
    </section>
  );
};

SectionMenu.propTypes = {
  scrollToDown: PropTypes.func.isRequired
};

export default SectionMenu;
