import React, { PureComponent } from "react";
import About from "../../components/About/About";
import Gallery from "../../components/Gallery/Gallery";
import Map from "../../components/Map/Map";

class AboutPage extends PureComponent {
  state = {
    isModalGalleryOpen: false,
    selectedImgInd: null
  };

  openModal = ind => {
    this.setState({
      isModalGalleryOpen: true,
      selectedImgInd: ind
    });
  };

  closeModal = () => {
    this.setState({
      isModalGalleryOpen: false
    });
  };

  showLargeImg = evt => {
    const { name } = evt.target.parentNode;

    this.setState(prevState => ({
      selectedImgInd:
        name === "next"
          ? prevState.selectedImgInd + 1
          : prevState.selectedImgInd - 1
    }));
  };

  render() {
    const { isModalGalleryOpen, selectedImgInd } = this.state;

    return (
      <div>
        <About />
        <Gallery
          isModalGalleryOpen={isModalGalleryOpen}
          openModal={this.openModal}
          closeModal={this.closeModal}
          selectedImgInd={selectedImgInd}
          showLargeImg={this.showLargeImg}
        />
        <Map />
      </div>
    );
  }
}

export default AboutPage;
