import React from "react";
import { NavLink } from "react-router-dom";
import PropTypes from "prop-types";
import style from "./SectionEvents.module.css";
import events from "../../helpers/events.json";
import SectionEventsItem from "./SectionEventsItem/SectionEventsItem";

const SectionEvents = ({ scrollToDown }) => {
  const eventsForRender = [];
  let i = 0;
  while (i < 3) {
    eventsForRender.push(events[i]);
    i += 1;
  }
  return (
    <div className={style.events}>
      <p className={style.headline}>Upcoming Events</p>
      <ul className={style.eventsList}>
        {eventsForRender.map(el => (
          <SectionEventsItem
            key={el.id}
            id={el.id}
            title={el.title}
            image={el.image}
            date={el.date}
            time={el.time}
            description={el.description}
          />
        ))}
      </ul>
      <NavLink
        className={style.linkShowAllEvents}
        to="/events"
        onClick={scrollToDown}
      >
        Show all events
      </NavLink>
    </div>
  );
};

SectionEvents.propTypes = {
  scrollToDown: PropTypes.func.isRequired
};

export default SectionEvents;
