import React from "react";
import { NavLink } from "react-router-dom";
import PropTypes from "prop-types";
import style from "./SectionAbout.module.css";

const SectionAbout = ({ scrollToDown }) => {
  return (
    <section className={style.wrapper}>
      <div className={style.about}>
        <div className={style.description}>
          <h3 className={style.headline}>About Voila Restaurant</h3>
          <p className={style.aboutText}>
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Donec
            convallis lorem nec enim tempus viverra condimentum eget dui.
            Quisque ac ornare orci lacinia facilisis velit{" "}
            <span className={style.aboutTextDecor}>eget interdum</span>.
          </p>
          <p className={style.aboutText}>
            Quisque orci neque, euismod vitae turpis quis, accumsan iaculis
            velit. Nulla lacinia facilisis velit eget interdum.
          </p>
          <NavLink
            to="/about"
            className={style.linkAbout}
            onClick={scrollToDown}
          >
            read more
          </NavLink>
        </div>
        <div className={style.pictures}>
          <img
            className={style.pic1}
            src="../../images/about-1.jpg"
            alt=""
            width="636"
            height="498"
          />
          <div className={style.bottomPictures}>
            <img className={style.pic2} src="../../images/about-2.jpg" alt="" />
            <img className={style.pic3} src="../../images/about-3.jpg" alt="" />
            <img className={style.pic4} src="../../images/about-4.jpg" alt="" />
          </div>
        </div>
      </div>
    </section>
  );
};

SectionAbout.propTypes = {
  scrollToDown: PropTypes.func.isRequired
};

export default SectionAbout;
