import React from "react";
import style from "./SectionBooking.module.css";

const SectionBooking = () => {
  return (
    <section className={style.booking}>
      <div className={style.bookTable}>
        <p className={style.headline}>Book a Table</p>
        <p className={style.description}>
          Like our restaurant concept? Book a table and taste our dishes on
          first hand.
        </p>
        <button type="button" className={style.buttonBookTable}>
          book a table
        </button>
      </div>
      <div className={style.bookingOr} />
      <div className={style.tellFriends}>
        <p className={style.headline}>Tell To Your Friends</p>
        <p className={`${style.description} ${style.tellDescription}`}>
          Some of your friends may like to visit if you recommend us. They’ll
          trust your taste!
        </p>
        <div className={style.buttons}>
          <button
            type="button"
            className={`${style.buttonTellFriends} ${style.buttonTellFriendsFacebook}`}
          >
            Share
          </button>
          <button
            type="button"
            className={`${style.buttonTellFriends} ${style.buttonTellFriendsTweet}`}
          >
            Tweet
          </button>
        </div>
      </div>
    </section>
  );
};

export default SectionBooking;
