import React from "react";
import { NavLink } from "react-router-dom";
import PropTypes from "prop-types";
import style from "./Footer.module.css";

const Footer = ({ scrollToTop, scrollToDown }) => {
  return (
    <footer className={style.footer}>
      <div className={style.footerOverlay}>
        <NavLink to="/" className={style.logo} onClick={scrollToTop}>
          Voilà
        </NavLink>
        <div className={style.navigation}>
          <p className={style.footerNav}>navigation</p>
          <ul className={style.navList}>
            <li className={style.navListItem}>
              <NavLink
                to="/menu"
                className={style.navLink}
                onClick={scrollToDown}
              >
                menu
              </NavLink>
            </li>
            <li className={style.navListItem}>
              <NavLink
                to="/about"
                className={style.navLink}
                onClick={scrollToDown}
              >
                about
              </NavLink>
            </li>
            <li className={style.navListItem}>
              <NavLink
                to="/events"
                className={style.navLink}
                onClick={scrollToDown}
              >
                events
              </NavLink>
            </li>
          </ul>
        </div>
        <div className={style.contact}>
          <p className={style.visit}>Contact</p>
          <p className={`${style.footerDescr} ${style.footerDescription}`}>
            6-8 RUE DUPHOT, 75001 PARIS, FRANCE
          </p>
          <p className={style.footerDescr}>+33 (0)1 52 45 89 16</p>
          <p className={style.connect}>connect with us</p>
          <div className={style.socials}>
            <button
              type="button"
              className={`${style.socialsIcon} ${style.iconFacebook}`}
            />
            <button
              type="button"
              className={`${style.socialsIcon} ${style.iconTwitter}`}
            />
            <button
              type="button"
              className={`${style.socialsIcon} ${style.iconInstagram}`}
            />
            <button
              type="button"
              className={`${style.socialsIcon} ${style.iconTrip}`}
            />
          </div>
        </div>
      </div>
    </footer>
  );
};

Footer.propTypes = {
  scrollToTop: PropTypes.func.isRequired,
  scrollToDown: PropTypes.func.isRequired
};

export default Footer;
