import React from "react";
import PropTypes from "prop-types";
import { NavLink } from "react-router-dom";
import style from "./Header.module.css";
import Modal from "../Modal/Modal";

const Header = ({
  isModalOpen,
  openModal,
  closeModal,
  scrollDownForDetails,
  changeDate,
  date,
  changeTime,
  time,
  name,
  tel,
  numberOfPeople,
  comments,
  handleChange,
  handleSubmit,
  isEmptyInput
}) => {
  return (
    <header className={style.wrapper}>
      <div className={style.header}>
        <nav className={style.pageNav}>
          <NavLink to="/" exact className={style.logo}>
            Voilà
          </NavLink>
          <div className={style.pageNavOverlay}>
            <ul className={style.navigation}>
              <li className={style.navigationItem}>
                <NavLink
                  to="/menu"
                  className={style.navigationLink}
                  onClick={scrollDownForDetails}
                >
                  menu
                </NavLink>
                <p className={style.navigationdescr}>
                  Sit amet constetur adipisicing elit. Fugit perspiciatis.
                </p>
              </li>
              <li className={style.navigationItem}>
                <NavLink
                  to="/about"
                  className={style.navigationLink}
                  onClick={scrollDownForDetails}
                >
                  about
                </NavLink>
                <p className={style.navigationdescr}>
                  Adipisicing elit. Fugit pipsam ratione numquam
                </p>
              </li>
              <li className={style.navigationItem}>
                <NavLink
                  to="/events"
                  className={style.navigationLink}
                  onClick={scrollDownForDetails}
                >
                  events
                </NavLink>
                <p className={style.navigationdescr}>
                  Ratione numquam lorem ipsum dolor sit amet tetur.
                </p>
              </li>
            </ul>
          </div>
        </nav>
        <h1 className={style.headline}>Welcome to Voilà</h1>
        <h2 className={style.description}>
          We offer mouth watering{" "}
          <span className={style.descriptionDecor}>French cuisine</span>
        </h2>
        <div className={style.buttons}>
          <NavLink
            to="/menu"
            className={`${style.menuLink} ${style.button}`}
            onClick={scrollDownForDetails}
          >
            see menu
          </NavLink>
          <button type="button" className={style.button} onClick={openModal}>
            find a table
          </button>
        </div>
        <button
          type="button"
          className={style.scroll}
          onClick={scrollDownForDetails}
        >
          Scroll down for details
        </button>
      </div>
      {isModalOpen && (
        <Modal
          closeModal={closeModal}
          changeDate={changeDate}
          date={date}
          changeTime={changeTime}
          time={time}
          name={name}
          tel={tel}
          numberOfPeople={numberOfPeople}
          comments={comments}
          handleChange={handleChange}
          handleSubmit={handleSubmit}
          isEmptyInput={isEmptyInput}
        />
      )}
    </header>
  );
};

Header.defaultProps = {
  date: null,
  time: null,
  name: "",
  tel: "",
  numberOfPeople: "",
  comments: "",
  changeTime: () => {}
};

Header.propTypes = {
  isModalOpen: PropTypes.bool.isRequired,
  openModal: PropTypes.func.isRequired,
  closeModal: PropTypes.func.isRequired,
  scrollDownForDetails: PropTypes.func.isRequired,
  changeDate: PropTypes.func.isRequired,
  date: PropTypes.shape(),
  changeTime: PropTypes.func,
  time: PropTypes.shape(),
  name: PropTypes.string,
  tel: PropTypes.string,
  numberOfPeople: PropTypes.string,
  comments: PropTypes.string,
  handleChange: PropTypes.func.isRequired,
  handleSubmit: PropTypes.func.isRequired,
  isEmptyInput: PropTypes.bool.isRequired
};

export default Header;
