import React, { PureComponent, createRef } from "react";
import PropTypes from "prop-types";
import style from "./ModalGallery.module.css";

class ModalGallery extends PureComponent {
  backdropRef = createRef();

  UNSAFE_componentWillMount() {
    window.removeEventListener("keydown", this.handlePressKey);
  }

  componentDidMount() {
    window.addEventListener("keydown", this.handlePressKey);
  }

  handlePressKey = evt => {
    if (evt.code !== "Escape") return;
    this.props.closeModal();
  };

  handleBackdropClick = evt => {
    const { current } = this.backdropRef;
    if (current && evt.target !== current) return;
    this.props.closeModal();
  };

  render() {
    const { images, selectedImgInd, showLargeImg } = this.props;

    return (
      <div
        className={style.overlay}
        role="button"
        tabIndex={0}
        ref={this.backdropRef}
        onClick={this.handleBackdropClick}
      >
        <div className={style.modal}>
          <button
            type="button"
            name="prev"
            className={style.btnPrev}
            onClick={showLargeImg}
            disabled={selectedImgInd === 0}
          >
            <i className={`material-icons ${style.icon}`}>arrow_back_ios</i>
          </button>
          <img
            className={style.modalImg}
            src={images[selectedImgInd].image}
            alt=""
          />
          <button
            type="button"
            name="next"
            className={style.btnNext}
            onClick={showLargeImg}
            disabled={selectedImgInd === images.length - 1}
          >
            <i className={`material-icons ${style.icon}`}>arrow_forward_ios</i>
          </button>
        </div>
      </div>
    );
  }
}

ModalGallery.propTypes = {
  closeModal: PropTypes.func.isRequired,
  images: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string.isRequired,
      image: PropTypes.string.isRequired
    }).isRequired
  ).isRequired,
  selectedImgInd: PropTypes.number.isRequired,
  showLargeImg: PropTypes.func.isRequired
};

export default ModalGallery;
