import React, { PureComponent } from "react";
import Events from "../../components/Events/Events";
import events from "../../helpers/events.json";

class EventsPage extends PureComponent {
  state = {
    selectedEventsIds: []
  };

  showDescription = id => {
    this.setState(prevState => ({
      selectedEventsIds: [...prevState.selectedEventsIds, id]
    }));
  };

  closeDescription = id => {
    const { selectedEventsIds } = this.state;
    const newSelectedEventsIds = selectedEventsIds.filter(el => el !== id);

    this.setState({
      selectedEventsIds: newSelectedEventsIds
    });
  };

  render() {
    const { selectedEventsIds } = this.state;

    return (
      <Events
        events={events}
        selectedEventsIds={selectedEventsIds}
        showDescription={this.showDescription}
        closeDescription={this.closeDescription}
      />
    );
  }
}

export default EventsPage;
