import React from "react";
import PropTypes from "prop-types";
import style from "./HomePage.module.css";
import SectionAbout from "../../components/SectionAbout/SectionAbout";
import SectionMenu from "../../components/SectionMenu/SectionMenu";
import SectionReservation from "../../components/SectionReservation/SectionReservation";
import SectionEvents from "../../components/SectionEvents/SectionEvents";
import SectionBooking from "../../components/SectionBooking/SectionBooking";

const HomePage = ({
  scrollToDown,
  changeDate,
  date,
  changeTime,
  time,
  name,
  tel,
  numberOfPeople,
  comments,
  handleChange,
  handleSubmit,
  isEmptyInput
}) => {
  return (
    <div>
      <SectionAbout scrollToDown={scrollToDown} />
      <SectionMenu scrollToDown={scrollToDown} />
      <div className={style.reservation}>
        <div className={style.reservationOverlay}>
          <SectionReservation
            changeDate={changeDate}
            date={date}
            changeTime={changeTime}
            time={time}
            name={name}
            tel={tel}
            numberOfPeople={numberOfPeople}
            comments={comments}
            handleChange={handleChange}
            handleSubmit={handleSubmit}
            isEmptyInput={isEmptyInput}
          />
          <SectionEvents scrollToDown={scrollToDown} />
        </div>
      </div>
      <SectionBooking />
    </div>
  );
};

HomePage.defaultProps = {
  date: null,
  time: null,
  comments: ""
};

HomePage.propTypes = {
  scrollToDown: PropTypes.func.isRequired,
  changeDate: PropTypes.func.isRequired,
  date: PropTypes.object,
  changeTime: PropTypes.func.isRequired,
  time: PropTypes.object,
  name: PropTypes.string.isRequired,
  tel: PropTypes.string.isRequired,
  numberOfPeople: PropTypes.string.isRequired,
  comments: PropTypes.string,
  handleChange: PropTypes.func.isRequired,
  handleSubmit: PropTypes.func.isRequired,
  isEmptyInput: PropTypes.bool.isRequired
};

export default HomePage;
